<template>
    <div class="io-container">
        <div class="io-box">
            <div class="inner-box">
                <div class="io-line" v-for="i of 8" :key="i">
                    <span>O{{ 9-i }}</span>
                    <span></span>
                    <span>I{{ 9-i }}</span>
                </div>
            </div>
            <div class="left-box">
                <div class="io-left-line" v-for="i of 8" :key="i">
                    <span class="left-input" @click="(showinpt = i + 1) && isdebugmode"
                        :class="{ 'green-border': otputStatus[8-i], 'red-border': !otputStatus[8-i] }">{{ getIoName(1, 8-i) }}</span>
                    <span class="circle" v-if="showCircle(8-i)"></span>
                    <span :class="{ 'left-line': true, red: !otputStatus[8-i] }"></span>
                </div>
            </div>
            <div class="right-box">
                <div class="io-right-line" v-for="i of 8" :key="i">
                    <span class="right-edit" v-if="(showotpt == i + 1) && isdebugmode">
                        <LeetxSwitch :module="module" :value="inputStatus[8-i]" :activeVal="1" :staticVal="0" :height="2"
                            @sentVal="debugIo($event, 1, i)"></LeetxSwitch>
                    </span>
                    <span class="right-input" @click="showotpt = i + 1"
                        :class="{ 'green-border': inputStatus[8-i], 'red-border': !inputStatus[8-i] }">{{ getIoName(0, 8-i) }}</span>
                    <span :class="{ 'right-line': true, 'green-bgc': inputStatus[8-i], red: !inputStatus[8-i] }"></span>
                </div>
            </div>
            <div class="IO">IO</div>
        </div>
        <!-- <div class="io-debug-mode">
            <span style="margin-right: 6.1rem;">启用IO调试</span>
            <LeetxSwitch :module="module" :value="isdebugmode" :activeVal="1" :staticVal="0" @sentVal="debugIn" :height="2">
            </LeetxSwitch>
        </div> -->
    </div>
</template>

<script>
import ioOtputSetting from "../../dialogs/settings/iootptsetting/iootptsetting";
import {
    fileBusWebsocket
} from "../../api/websocketApi.js";

import {
    busIoCfgUploadApi,
    busIoDebugInApi,
    busIoDebugOutApi,
    busIoModifyApi
} from "../../api/io/io";
import LeetxSwitch from "../switch/LeetxSwitch.vue";
import { getInputKey, getOutputKey } from '../../utils/io';
import { messageApi } from '../message/message.js'


export default {
    components: {
        LeetxSwitch,
    },
    data() {
        return {
            is_unmounted: false,
            showinpt: null,
            showotpt: null,
            inputStatus: { 0: false, 1: false, 2: false, 3: false, 4: false, 5: false, 6: false, 7: false },
            otputStatus: { 0: false, 1: false, 2: false, 3: false, 4: false, 5: false, 6: false, 7: false },
            dataId: undefined,
            con: undefined,
            ioTimer: undefined,
            canGetConfig: false,
            ioConfig: [[], []],
            module: "DEBUG",
            isdebugmode: false,
        };
    },
    methods: {
        edit() {
            ioOtputSetting().then(() => {
                // closeMask();
            }).catch(() => {
                // closeMask()
            });
        },
        debugIo(val, i, j) {
            if (!this.ioConfig[i][j]) {
                let msg = this.$t('dbgio.nouse')
                messageApi.warning([msg])
                return
            }
            let data = this.ioConfig[i][j]
            data.val = val
            busIoModifyApi(data).then((result) => {
                if (result.status == 0) {
                    let msg = this.$t('dbgio.sucess')
                    messageApi.success([msg])
                } else {
                    //status: 9104 || 9105 
                    let msg = this.$t('dbgio.err')
                    messageApi.warning([msg])
                }
            }).catch(() => {
                let msg = this.$t('dbgio.err')
                messageApi.warning([msg])
            });
        },
        // 获取IO数据
        getIoData() {
            this.ioTimer = setInterval(() => {
                this.dataId = Math.random();
                fileBusWebsocket.send(
                    JSON.stringify({
                        id: this.dataId,
                        mode: "IO",
                    })
                );
            }, 200);
        },
        // 创建io-websocket
        createWs() {
            fileBusWebsocket.msghandler = (msg) => {
                var tmp = JSON.parse(msg);
                if (tmp.id == this.dataId && tmp.mode == "IO") {
                    this.inputStatus = tmp.data.in
                    this.otputStatus = tmp.data.out
                    this.canGetConfig = true
                }
            };
        },
        getIoConfig() {
            busIoCfgUploadApi().then((result) => {
                let inData
                let otData
                if (result.data.ToolIn) {
                    inData = result.data.ToolIn.Member
                    for (let i = 0; i < inData.length; i++) {
                        if (!getInputKey(inData[i].LinkSdo)) continue
                        let index = inData[i].StartByte * 10
                        this.ioConfig[0][index] = { "addr": `I${index + 1}`, "sdo": inData[i].LinkSdo, "val": undefined, 'Not': inData[i].Not }
                    }
                }
                if (result.data.ToolOut) {
                    otData = result.data.ToolOut.Member
                    for (let i = 0; i < otData.length; i++) {
                        if (!getOutputKey(otData[i].LinkSdo)) continue
                        let index = (otData[i].StartByte * 10)
                        this.ioConfig[1][index] = { "addr": `O${index + 1}`, "sdo": otData[i].LinkSdo, "val": undefined }
                    }
                }
                this.createWs();
            })
        },
        getIoName(i, j) {
            let item = this.ioConfig[i][j]
            if (item) {
                return i == 0 ? getInputKey(item.sdo).name : getOutputKey(item.sdo).name
            }
            return ""
        },
        debugIn() {
            this.isdebugmode = !this.isdebugmode;
            if (this.isdebugmode)
                busIoDebugInApi();
            else {
                busIoDebugOutApi();
            }
        },
        debugOut() {
            busIoDebugOutApi()
        },
        showCircle(i) {
            if (this.ioConfig[0].length > 0 && this.ioConfig[0][i]) {
                return this.ioConfig[0][i]['Not'] == 1
            }
            return false
        }
    },
    created() {
        this.getIoData();
        this.getIoConfig()
    },
    unmounted() {
        // console.log('io unmounted ');
        clearInterval(this.ioTimer);
        // this.debugOut()

    },
    mounted() {

    },
};
</script>

<style scoped>
