    margin: auto;
    padding-top: 2rem;
}

.right-input {
    left: 5vw;
    width: 9.25rem;
    height: 6.045rem;
    margin: auto;
    padding-top: 2rem;
}

.left-line,
.right-line {
    position: absolute;
    top: 50%;
